body{

    margin: 0;
    padding: 0;

}

.container-show-movie{

    width: 100% auto;
    height: 100%;
    padding: 50px;

    display: flex;
    flex-wrap: wrap;
    justify-content: left;

    background-color: rgb(46, 45, 45);

}
.container-card{

    text-decoration: none;

}
.movie-card{

    width: 250px;
    height: 300px;
    margin-left: 25px;
    margin-top: 25px;

    border-radius: 25px;

    /* display: flex;
    flex-direction: column; */

    /* background-color: bisque; */
    transition: all ease-in-out 0.15s;

}
.movie-card:hover{

    width: 255px;
    height: 305px;
    box-shadow: 0px 1px 8px 8px #FFF;
    transition: all ease-in-out 0.15s;

}
.container-img-movie{

    width: 100%;
    height: 100%;

    border-radius: 25px 25px 25px 25px;

    /* background-color: red; */

}
.poster{

    width: 100%;
    height: 100%;
    border-radius: 25px 25px 25px 25px;
    /* object-fit: cover; */

}

.container-title-movie{

    width: 100% auto;
    height: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px; 
    padding-right: 10px;
    border-radius: 0px 0px 25px 25px;
    margin-top: -89.635px;
    background-color: black;
    opacity: 80%;

}
.container-title-movie p{

    text-align: center;
    color: whitesmoke;
    text-decoration: none;
    font-size: 16px;
    font-family: sans-serif;

}
body{

